<!-- 正在使用 -->
<template>
    <div class="mask" v-if="isShow">
        <div class="program-box">
            <div class="program-title-line">
                <span class="program-title-msg">{{ $t('newpro.newpro') }}</span>
                <span class="program-box-close" @click="cancel">
                    <svgloader class="icon-close" icon="Close" size="1.2rem"></svgloader>
                </span>
            </div>
            <div class="program-copy-line">
                <div class="program-copy-text">{{ $t('newpro.cpexistpro') }}</div>
                <LeetxSwitch class="program-copy-btn" :height="2.5" v-model="isCopyFile" :alive="allowCopyFile"
                    :clbck="promptUser"></LeetxSwitch>
            </div>
            <div class="program-mode-line">
                <div class="program-copy-text">{{ modename }}</div>
                <NewSelect :width="18.4769" v-if="!isCopyFile" class="program-select-mode" v-model:realVal="curMode"
                    :options="modes"></NewSelect>
                <NewSelect :isScroll="true" :width="18.4769" v-else class="program-select-mode" :realVal="sourcePset" :options="profilePsets"
                    @valChng="chngPf($event)"></NewSelect>
            </div>
            <div class="program-name-line">
                <div class="program-copy-text">{{ $t('newpro.proname') }}</div>
                <InputText :text="newName.text" :placeholder="$t('newpro.name')" class="program-name-input"
                    v-model="newName" :textValid="checkName()" :validReason="nameError"></InputText>
            </div>
            <div class="program-pset-line">
                <div class="program-copy-text">Pset</div>
                <div class="program-pset-input">
                    <Counter _width="100%" :cnum="newPset.text" :_min="1" :_max="999999999" @update='updateNewPset($event)'
                        _ftszie="2rem" :textValid="checkPset()" :validReason="$t('newpro.pset')"></Counter>
                </div>
            </div>

            <div class="program-operate-line">
                <div class="program-cancel-btn" @click="cancel">{{ $t('newpro.cancel') }}</div>
                <div class="program-cnfrm-btn" @click="sbmtNewPrgrmData">{{ $t('newpro.confirm') }}</div>
            </div>
        </div>
    </div>
</template>

<script>

import InputText from "@/components/input/InputText.vue"
import svgloader from '@/components/icon_font/svgloader.vue'
import LeetxSwitch from '@/components/switch/LeetxSwitch.vue'
import NewSelect from '@/components/select/NewSelect.vue'
import Counter from '@/components/controls/Counter.vue'
import { curPfPsetApi } from '@/api/profile/profile'
import { deepCopy } from '@/utils/deepCopy'
import langvue from '@/plugins/langVue'
import { messageApi } from '@/components/message/message'
// import getEmptyProfile from '@/utils/empty_profile'
import { genProfile } from '@/components/profiles/profileHelper'

export default {
    components: {
        svgloader, LeetxSwitch, InputText, Counter,
        NewSelect
    },
    data() {
        return {
            // title:"新建程序",
            isShow: true,
            // 选择的模式
            curMode: 2,
            // 是否复制工艺文件
            isCopyFile: 0,
            // 当前文件
            Profile: undefined,
            // 源pset
            sourcePset: -1,
            box: undefined,
            profilePsets: {},
            // 新的文件名
            newName: {
                'text': "",
                "valid": false
            },
            // 新的pset
            newPset: {
                'text': 1,
                "valid": false
            },
        }
    },
    props: {
        // 可选的模式
        modes: {
            type: Object,
            default: () => {
                //props中访问不到this
                let mode = langvue.global.t('dlgprogram.expertsMode')
                let fastMode=langvue.global.t('dlgprogram.fastMode')

                return {
                    [fastMode]: 1,
                    [mode]: 2,
                    "自学习模式": 3,
                }
            }
        },
        // 文件列表
        pfList: {
            type: Array,
            default: function () {
                return []
            }
        },
    },
    methods: {
        // 确认按钮
        sbmtNewPrgrmData() {
            if (!this.checkName() || !this.checkPset()) { return }
            // 这里还需要把具体的工艺文件内容复制下来给新的文件
            this.isShow = false
            let pset = this.newPset.text
            let name = this.newName.text
            this.Profile = genProfile(this.curMode);
            // 是否复制工艺,先拿到要复制的工艺然后更新文件名,pset,再提交文件
            if (this.isCopyFile != 0) {
                let success = (file) => {
                    this.Profile = deepCopy(file)
                    this.Profile.Name = name
                    this.Profile.Pset = pset
                    this.onConfirm && this.onConfirm({ 'Profile': { 'Profile': this.Profile }, pset })
                }
                let failure = () => {
                    let msg = this.$t('dlgprogram.copyFail')
                    this.onCancel && this.onCancel(msg)
                }
                this.getFileContentByPsetApi(this.sourcePset, success, failure)
            } else {
                this.Profile.Name = name
                this.Profile.Pset = pset
                this.Profile.Mode = this.curMode
                this.onConfirm && this.onConfirm({ 'Profile': { 'Profile': this.Profile }, pset })
            }
        },
        // 取消新建文件
        cancel() {
            this.isShow = false
            let msg = this.$t('dlgprogram.userCancel')
            this.onCancel && this.onCancel(msg)
        },
        // 切换文件
        chngPf(pset) {
            //this.源Pset  =选中的pset
            this.sourcePset = pset
            this.setPset()
            this.setName()
        },
        // 关闭添加工艺的对话框
        closeboard(e) {
            let x = e.clientX
            let y = e.clientY
            let that = this
            let left = that.box.offsetLeft - that.box.offsetWidth / 2
            let right = that.box.offsetLeft + that.box.offsetWidth - that.box.offsetWidth / 2
            let top = that.box.offsetTop
            let btm = that.box.offsetTop + that.box.offsetHeight
            if ((x * y != 0) && (x <= left || x >= right || y >= btm || y <= top)) {
                window.removeEventListener('click', this.closeboard)
                that.isShow = false
            }
        },
        getBox() {
            this.box = document.getElementsByClassName("program-box")[0]
        },
        // 通过pset获取文件内容
        getFileContentByPsetApi(pset, success, failure) {
            curPfPsetApi(pset).then((result) => {
                if (result.status == 0) {
                    success && success(result.data.Profile)
                }
                else {
                    failure && failure()
                }
            }).catch((err) => {
                (failure && failure(err))
            });
        },
        // 用户手动更新pset
        updateNewPset(pset) {
            this.newPset.text = pset
        },
        // 提醒用户
        promptUser() {
            if (!this.allowCopyFile) {
                messageApi.warning(['没有工艺可复制'])
            }
        },
        // 初始化一个可用文件
        initDefaultProfile() {
            this.setPset()
            this.setName()
        },
        // 更新文件名
        setName() {
            // if (this.checkName()) {
            //     this.newName.text = 'pset1';
            // }
            // else {
            //     this.newName.text = `pset${this.newPset.text}`
            // }
            if(this.checkName()==false) return
        
            this.newName.text = `pset${this.newPset.text}`
            // this.newName.text=randomString(5,6)
        },
        // 更新文件pset
        setPset() {
            if (this.checkPset()) return
            this.newPset.text = 1
            for (let i in this.pfList) {
                if (this.pfList[i].pset !== this.newPset.text) {
                    break
                }
                this.newPset.text = this.newPset.text + 1
            }
        },
        // 检测文件名是否合法
        checkName() {
            for (let item of this.pfList) {
                if ( item.name === this.newName.text) {
                    return false
                }
            }
            return true
        },
        // 检测文件pset是否合法
        checkPset() {
            for (let item of this.pfList) {
                if (item.pset === this.newPset.text || this.newPset.text === "" || this.newPset.text>255) {
                    return false
                }
            }
            return true
        },
        // 初始化文件名与pset的可选列表
        initNameWithPset() {
            for (let i in this.pfList) {
                let item = this.pfList[i]
                let pset = item.pset
                let name = item.name
                this.profilePsets[`${pset}[${name}]`] = item.pset
            }
        },
    },
    computed: {
        modename() {
            if (this.isCopyFile) {
                // 源pset
                return this.$t('newpro.src')
            }
            //模式
            return this.$t('newpro.mode')
        },
        // 是否允许复制文件
        allowCopyFile() {
            return this.pfList.length > 0
        },
        // 文件名不合法原因
        nameError() {
            if (this.newName.text == "") {
                return this.$t('dlgprogram.nameNotEmpty')
            }
            return this.$t('dlgprogram.nameRepeat')
        }
    },
    watch: {
        pfList: {
            immediate: true,
            deep: true,
            handler: function () {
                this.initDefaultProfile()
                this.initNameWithPset()
            }
        },
        isCopyFile() {
            if (this.isCopyFile && this.pfList.length > 0) {
                this.chngPf(this.pfList[0].pset)
            }
        }
    },
    mounted() {
        setTimeout(() => {
            this.getBox()
            window.addEventListener('click', this.closeboard)
        }, 50)
    },
}
</script>
<style  scoped>
